@import url(https://fonts.googleapis.com/css2?family=Neucha&family=Roboto&family=Rubik&family=Rubik+Bubbles&display=swap);
body {
    padding: 0;
    margin: 0;
    font-family: 'Rubik Bubbles', cursive;
}

.App {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.top-container {
    position: relative;
    min-height: 80vh;
    
}

.top-container:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: url(/rotaract/static/media/bg1.ce67dba5.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: 1;
    opacity: 0.5;
}

.appbar {
    position: relative;
    z-index: 2;
}
.menubar a{
    color: #ffffffb9;
    text-decoration: none;
    font-weight: 400;
    transition: .2s;
}

.menubar a:hover {
    opacity: .8;
}
.profiles{
    margin: 20px auto;
}



/* 
    gold: #a18b60
    blue: #15448a
    pink: #d21a58
*/



@media (max-width: 800px) {
    .menubar a {
        font-size: small;
    }
    .clubtitle h2{
        font-size: 2rem;
    }
    .clubtitle h4{
        font-size: 1.8rem;
    }
}
